<docs>
  # HeroHome
  > The hero for homepage
  
  @author Kffein <contact@kffein.com>
</docs>

<script lang="ts">
import { templateRef, VueInstance } from "@vueuse/core";
import arrow from 'assets/svg/arrow.svg?raw';
import { gsap } from "vendors/gsap";
import { defineComponent, toRefs } from 'vue';
//import { STEPS } from "../webgl/LineScene";
import useFlow from 'utils/flow';

export default defineComponent({
  name: "HeroHome",
  props: {
    title: {
      type: String,
      default: '',
    },
    subtitle: {
      type: String,
      default: '',
    },
    description: {
      type: String,
      default: '',
    },
    cta: {
      type: String,
      default: '',
    },
  },
  emits: ["discover"],
  setup(props, { emit }) {
    const { onInitUi } = useFlow();
    const el = templateRef<HTMLElement>("el");
    const discover = templateRef<VueInstance>("Discover");
    const title = templateRef<HTMLElement>("Title");
    const subtitle = templateRef<HTMLElement>("Subtitle");
    const descriptionEl = templateRef<HTMLElement>("Description");

    onInitUi(() => {
      gsap.timeline()
        .set(title.value, { scale: .9, opacity: 0, })
        .set(subtitle.value, { yPercent: -50, opacity: 0, })
        .set(descriptionEl.value, { yPercent: 50, opacity: 0, })
        .set(discover.value?.$el, { yPercent: -50, opacity: 0, });
    });

    // Tween to display hero elements 
    const animateIn = () => {
      gsap.timeline()
        .add(() => {
          el.value.classList.remove("hidden")
        }, 0)
        .to(title.value, { scale: 1, opacity: 1, ease: "power4.inOut", duration: 1.5 }, 0)
        .to(subtitle.value, { yPercent: 0, opacity: 1, ease: "power4.out", duration: 1.5 }, .8)
        .to(descriptionEl.value, { yPercent: 0, opacity: 1, ease: "power4.out", duration: 1.5 }, .8)
        .to(discover.value?.$el, { yPercent: 0, opacity: 1, ease: "power4.out", duration: 1.5 }, .8);
    }

    // Tween to hide hero elements 
    const animateOut = () => {
      gsap.timeline()
        .to(el.value, { opacity: 0, clearProps: "all", ease: "power4.out", duration: .7 }, 0)
        .add(() => {
          el.value.classList.add("hidden")
        }, .5)
    }

    const launchAnimation = () => {
      emit('discover');
    }

    return {
      ...toRefs(props),
      //
      animateIn,
      animateOut,
      launchAnimation,
      arrow
    };
  },
});
</script>

<template>
  <section ref="el"
           class="heroHome"
           :data-dark="false"
  >
    <span ref="Subtitle"
          class="subtitle"
          v-html="subtitle"
    />
    <h1 ref="Title"
        class="title"
        v-html="title"
    />
    <div ref="Description"
         class="description"
         v-html="description"
    />

    <ui-button ref="Discover"
               class="cta"
               theme="primary"
               :icon="arrow"
               direction="right"
               :label="cta"
               @click="launchAnimation"
    />
  </section>
</template>

