<docs>
  # PageHome
  > The homepage
  
  @author Kffein <contact@kffein.com>
</docs>

<script lang="ts">
import { templateRef, toRefs, tryOnUnmounted, until, VueInstance } from '@vueuse/core';
import SoundOffSVG from "assets/svg/sound_off.svg?raw";
import SoundOnSVG from "assets/svg/sound_on.svg?raw";
import axios from 'axios';
import { WebGLSceneInterface } from 'components/WebGL/gl';
import { store as MenuStore } from 'components/ui/Header/store';
import loaderStore from 'components/ui/Loader/store';
import emitter, { EVENTS } from "plugins/store/events";
import { clearWebGL, getActiveSceneAt, updateBgColor, useWebGL } from 'plugins/webgl/three';
import { store as appStore } from 'store/app';
import { store as interfaceStore } from "store/interface";
import SoundButton from "ui/SoundButton.vue";
import usePageFlow from 'utils/flow/page';
import { gsap } from "vendors/gsap";
import { computed, defineComponent, provide, ref, watch, WatchStopHandle } from 'vue';
import { useRouter } from 'vue-router';
import Hero from './blocks/Hero.vue';
import imageClaude from './img/MH-CGingras-184.jpg';
import imageBackground from './img/background-image.png';

export interface VueAnimate extends VueInstance {
  animateIn(): void;
  animateOut(): void;
}
export default defineComponent({
  name: "PageHome",
  components: {
    Hero,
    SoundButton
  },
  setup() {
    const router = useRouter();
    const toobig = computed(() => interfaceStore.isViewportTooBig);
    const { done, loaded } = toRefs(loaderStore.state);
    const Hero = templateRef<VueAnimate>("Hero");
    const el = templateRef<HTMLElement>("el");
    const BackgroundImage = templateRef<VueInstance>("BackgroundImage");
    const ClaudeImage = templateRef<VueInstance>("ClaudeImage");
    const isLeaving = ref(false);
    const Scene = templateRef<HTMLElement>("Scene");

    const { onInitUi, getData, readyToRender } = usePageFlow<PageHome>({
      load() {
        return axios.get(`/home/data.json`)
          .then(({ data }) => {
            return {
              ...data
            }
          })
      },
      ui: {
        library: () => import("./blocks/webgl/LibraryScene"),
        line: () => import("./blocks/webgl/LineScene"),
      },
    });

    const visibleSceneIndex = ref(appStore.introSeen ? 1 : 0);

    let stopWatchInactive : WatchStopHandle | null = null;

    onInitUi(async ({ ui }) => {

      await until(done).toBe(true);

      useWebGL(
        [
          {
            glScene: ui.line.default,
            disabled: visibleSceneIndex.value !== 0,

          },
          {
            glScene: ui.library.default,
            disabled: visibleSceneIndex.value !== 1
          },
        ], { antialias: true, canvasContainer: Scene.value, bgColor: '#192096', alpha: true },);

      appStore.setAppBgColor('255, 234, 249');
      emitter.on(EVENTS.CLICK_THEME, onClickTheme);
      emitter.on(EVENTS.PAGE_TRANSITION, onPageTransition);

      animate();

      // watch for inactivity
      stopWatchInactive = watch(
        computed( () => appStore.isInactive ),
        inactive => {
          if(visibleSceneIndex.value === 1) {
            if(inactive) {
              // mute the sound
              const sound = loaderStore.getSound("home-track");
              if(sound) {
                sound.off();
                sound.fade(.5, 0, 1000);

                sound.on("fade", () => {
                  sound.stop();
                });
              }
            }
            else{
              playSound();
            }
          }
        }
      )
    });

    const changeScene = (index: number) => {
      gsap.timeline()
        .add('start')
        .to(BackgroundImage.value, { scale: 1, opacity: 1, ease: "power4.inOut", duration: 1.5 }, 'start')
        .to(Scene.value, { scale: 1, opacity: 0, ease: "power4.inOut", duration: 1 }, 'start-=0.3')
        .set(ClaudeImage.value, { scale: 0.8, opacity: 0, ease: "power4.inOut" }, 0)
        .to(ClaudeImage.value, { scale: 0.8, opacity: 1, ease: "power4.inOut", duration: 1 }, 'start+=1.5')
        .to(ClaudeImage.value, { scale: 1, opacity: 1, ease: "power4.inOut", duration: 1.5 }, 'start+=3.5')
        .add(
          () => {
            visibleSceneIndex.value = index;
            let oldScene = getActiveSceneAt(index === 1 ? 0 : 1) as WebGLSceneInterface;
            oldScene.disabled = true;
            let scene = getActiveSceneAt(index) as WebGLSceneInterface;
            scene.disabled = visibleSceneIndex.value !== index;
            if (!scene.disabled) scene.resize();
            updateBgColor(index === 0 ? '#FFEAF9' : '#192096');

            appStore.setIntroSeen(true);
            playSound();

          }, 'start+=5.25'
        )
        .to(ClaudeImage.value, { scale: 1, opacity: 0, ease: "power4.inOut", duration: 1.5 }, 'start+=6.25')
        .add(() => {
          MenuStore.setHidden(false)
        }, 'start+=7.5')
        .to(Scene.value, { scale: 1, opacity: 1, ease: "power4.inOut", duration: 1 }, 'start+=6.2')


      if (index === visibleSceneIndex.value) return;

    }

    const animate = () => {
      const tl = gsap.timeline({
        delay: appStore.introSeen ? 0 : .2
      })

      if(!appStore.introSeen) {
        tl
          .fromTo(BackgroundImage.value, { opacity: 0 }, { opacity: 1, ease: "power4.out", duration: 1.5 }, .5)
          .add(() => {
            Hero.value?.animateIn();
          }, .5)
      }
      else{
        tl
          .fromTo(el.value.querySelector(".container-ratio"), {opacity:0}, {opacity:1, ease: "power4.out", duration: 1.5 }, .75)
          .fromTo(Scene.value, { opacity: 0 }, { opacity: 1, ease: "power2.inOut", duration: 1.5 }, .5)
          .add( () => {
            updateBgColor('#192096');
            playSound();
        }, 0)
          .add( () => {
            MenuStore.setHidden(false);
        }, .5)
      }
    }

    const onDiscover = () => {
      Hero.value?.animateOut();
      changeScene(1);

      //-- preload the rest of the spritesheets
      loaderStore.finishSpriteSheetsLoading();

      // -- play the sound
      const sound = loaderStore.getSound("home-track");
      if(sound) {
        sound.play();
        sound.stop();
      }
    }

    const page = getData();
    provide("page", page);

    const switchRoute = (route: any) => {
      switch (route) {
        default:
        case 'violon':
          router.push({ name: 'thematique', params: { slug: 'festival-de-lanaudiere' } });
          break;
        case 'livre':
          router.push({ name: 'thematique', params: { slug: 'charles-dutoit' } });
          break;
        case 'poster':
          router.push({ name: 'thematique', params: { slug: 'maria-callas' } });
          break;
        case 'typewriter':
          router.push({ name: 'thematique', params: { slug: 'metier-role-de-critique' } });
          break;
        case 'partitions':
          router.push({ name: 'thematique', params: { slug: 'yannick-nezet-seguin' } });
          break;
        case 'carnet':
          router.push({ name: 'thematique', params: { slug: 'universite-de-montreal' } });
          break;
        case 'box':
          router.push({ name: 'thematique', params: { slug: 'les-raretes' } });
          break
        case 'record':
          router.push({ name: 'thematique', params: { slug: 'oeuvres-preferees' } });
          break;

        case '/biographie/':
        case '/collection/':
        case '/credits/':
          router.push({ path: route });
          break;
      }
    }

    // Listen for the 'navigate' event
    const onClickTheme = (routeParams: any) => {
      const ContainerRatio = document.querySelector(".home .container-ratio");

      const tl = gsap.timeline()
        .add( () => {
          // mute the sound
          const sound = loaderStore.getSound("home-track");
          if(sound) {
            sound.off();
            sound.fade(.5, 0, 1000);
          }

          isLeaving.value = true;
        }, 0)
        .to(BackgroundImage.value, { opacity: 0, ease: "power4.out", duration: .75 }, 0)
        .add(() => {
          MenuStore.setHidden(true)
        }, 0)
        .add(() => {
          if(routeParams.bgColor) appStore.setAppBgColor(routeParams.bgColor)
        }, .7)
        .add(() => {
          switchRoute(routeParams.name)
        }, 1.1);


        if(ContainerRatio) {
          tl.to(ContainerRatio, {opacity:0, ease: "power4.out", duration: .75 }, 0)
        }
    }

    const playSound = () => {
      // play the sound
      const sound = loaderStore.getSound("home-track");
      if(sound) {
        sound.off();
        sound.play();
        sound.fade(0, .5, 2000);

        sound.on("end", () => {
          sound.play();
          sound.fade(0, .5, 2000);
        });
      }
    }

    const onPageTransition = (params:any) => {
      onClickTheme({
        name: params?.url,
        bgColor: params?.bgColor
      })
    }

    tryOnUnmounted(() => {
      emitter.off(EVENTS.CLICK_THEME, onClickTheme);
      emitter.off(EVENTS.PAGE_TRANSITION, onPageTransition);
      clearWebGL(true);

      const sound = loaderStore.getSound("home-track");
      if(sound) {
        sound.off();
        sound.stop();
      }

      stopWatchInactive?.();
    })

    return {
      //
      toobig,
      SoundOnSVG,
      SoundOffSVG,
      loaderDone: loaded,
      loaderFullyGone: done,
      readyToRender,
      page,
      Hero,
      introSeen: computed(() => appStore.introSeen),
      isLeaving,
      BackgroundImage,
      imageClaude,
      ClaudeImage,
      visibleSceneIndex,
      imageBackground,
      onDiscover
    }
  }
});
</script>

<template>
  <main v-if="readyToRender && page"
        ref="el"
        class="page home"
        :data-toobig="toobig"
        :data-variant="page.id"
        :data-scene="visibleSceneIndex"
  >
    <div class="container-ratio" :class="!loaderFullyGone ? 'hidden' : ''">
      <div ref="Scene" class="scene" />
      <SoundButton :noinvert="true" :data-hidden="!introSeen || isLeaving" />
    </div>
    <Hero v-if="!introSeen"
          v-bind="page"
          ref="Hero"
          :class="!loaderFullyGone ? 'hidden' : ''"
          @discover="onDiscover"
    />
    <div ref="BackgroundImage" :class="['backgroud-image', !loaderFullyGone ? 'hidden' : '']">
      <ui-picture :src="imageBackground"
                  cover="cover"
                  alt="background"
                  :lazy="false"
      />
    </div>
    <div ref="ClaudeImage" class="claude-image">
      <ui-picture :src="imageClaude"
                  cover="cover"
                  alt="Image de claude dans son bureau, entourÃ© de livres"
      />
    </div>
  </main>
</template>

