      border 1px solid $c-dark_blue_udm
      border-radius 10px
      overflow hidden
      min-height 'calc(100vh - 30px - %s)' % var(--header-height)


  :deep() .hidden.heroHome
    opacity 0
    pointer-events none
    trans()
